import LoginForm from '../components/loginForm/LoginForm';
import React from 'react';

const Login = () => {
    return (
        <main className='main bg-dark'>
            <section className='sign-in-content'>
                <i className='fa fa-user-circle sign-in-icon'></i>
                <h1>Sign In</h1>
                <LoginForm />
            </section>
        </main>
    );
}

export default Login;